<template>
<div class="row">
  <div style="margin-top:32px;" id="loader">
    <div style="display:none;" class="progress">
      <div class="indeterminate"></div>
    </div>
  </div>
    <div class="col s12 z-depth-4 card-panel">
      <form >
        <div class="row">
          <div class="input-field col s12 center">
            <h4>Login</h4>
          </div>
        </div>
        <div class="row margin">
          <div class="input-field col s12">
            <i class="material-icons prefix">email</i>
            <input id="email" type="text" v-model="email">
            <label for="email" class="center-align">Email</label>
          </div>
        </div>
        <div class="row margin">
          <div class="input-field col s12">
            <i class="material-icons prefix">lock</i>
            <input id="password" type="password" v-model="password">
            <label for="password" class="center-align">Password</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <button v-on:click="login" class="btn btn-large waves-effect waves-light blue accent-3 col s12">Login</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase';

export default {
 name: 'register',
  data() {
    return {
        email: '',
        password: ''
    };
  },
  methods: {
      login(e){
          
          firebase.auth().signInWithEmailAndPassword(this.email,this.password)
          .then(user => {
              alert(`logged in  ${user.user.email}`);
              this.$router.go({path: this.$router.path});
          })
          .catch(err=>{
              alert(err.message);
          });
          
          e.preventDefault();
          
      }
  }
};
</script>



