<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee_id" required>
                        <label>Employee ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s6">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                       
                    </div>
                    <div class="input-field col s6">
                        <input type="text" v-model="lastname" required>
                        <label>Last name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="dept" required>
                        <label>Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                        <label>Position</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="frequency_workouts" required>
                        <label>การออกกำลังกายที่ทำเป็นประจำ</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            lastname: null,
            dept: null,
            position: null,
            frequency_workouts: null
        }
    },
    methods: {
        saveEmployee() {
            db.collection('employee').add({
                employee_id: this.employee_id,
                name: this.name,
                lastname: this.lastname,
                dept: this.dept,
                position: this.position,
                frequency_workouts: this.frequency_workouts
            }).then(docRef=>this.$router.go({path : this.$router.path}))
            .catch(error=> console.log(error))
        }
    }
}
</script>




